import React, { Component } from 'react';
import { Draggable } from 'react-beautiful-dnd';
import Priority from '../Priority';


const styles = {

    title: {
        display: "inline-block",
        minWidth: 500,
        textAlign: "left"
    },
    completedStyle: {
        fontStyle: "italic",
        color: "#d35e0f",
        opacity: 0.4,
        textDecoration: "line-through",
        display: "flex",
        minWidth: 500,
        flex: 1

    }

}

export class TodoitemClass extends Component {
    state = {
        active: false,
        priority: "",
        titlePriotiry: {}
    }

    handleChange = (value) => {
        this.setState({ priority: value })
        if (value === 'low') return this.setState({ titlePriotiry: { opacity: 0.3 } });
        if (value === 'middle') return this.setState({ titlePriotiry: { opacity: 1 } })
        if (value === 'high') return this.setState({ titlePriotiry: { opacity: 1, fontWeight: 700 } })
    }


    render() {
        return (
            <Draggable
                key={this.props.index}
                draggableId={this.props.index + ''}
                index={this.props.index}>
                {(provided) => (<div ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}>
                    <li className={this.state.active ? 'todo-item active' : 'todo-item'}
                        style={this.state.titlePriotiry}
                        onTransitionEnd={() => this.props.handleDelete(this.props.id)}>

                        <input
                            type="checkbox"
                            checked={this.props.completed}
                            onChange={() => this.props.handleChange(this.props.id)}
                            className='input' />
                        <p style={this.props.completed ? styles.completedStyle : styles.title}>
                            {this.state.priority === 'high' && <span title='important Todo' style={this.state.titlePriotiry, { color: "red" }}>! </span>}
                            {this.props.title}
                        </p>
                        <Priority onChangeHandler={this.handleChange} />
                        <button
                            onClick={() => this.setState({ active: true })}
                            className='button'>
                            del
                        </button>
                    </li>
                </div>)}
            </Draggable>
        )
    }
}

export default TodoitemClass
